import React, {PureComponent} from 'react';
import MainMenu from '../common/MainMenu';

class ShopBackground extends PureComponent {
    render() {
        return (
            <div id="aboutBackground">
                <div className="subtitle">
                    # Shop
                </div>
                <div id="shopContent">
                    Coming Soon
                </div>
            </div>
        )
    }
}

class Shop extends PureComponent {
    render () {
        return (
            <>
                <MainMenu/>
                <ShopBackground/>
            </>
        );
    }
}

export default Shop;