import React, {PureComponent} from 'react';
import { NavLink} from 'react-router-dom';
import Dropdown from 'react-bootstrap/Dropdown';
import indazooLogo from '../../img/home/indazooLogo.png';


class MainMenu extends PureComponent {
    
    state = {
        mobileMenuClicked: false
        , artistMenuClicked: false
    }

    _handleMobileMenuClick = () => {
        this.setState({
            mobileMenuClicked: !this.state.mobileMenuClicked
            , artistMenuClicked: false
        });
    }

    _handleArtistMenuClick = () => {
        this.setState({
            artistMenuClicked: !this.state.artistMenuClicked
        });
    }

    handleMouseClick = (e) => {
        this.setState({
            aa: e.target.innerText
        })
        console.log(this.state);
    };

    handleMouseEnter = (e) => {
        e.target.style.color = '#ffc414';
    };
  
    handleMouseLeave = (e) => {
      e.target.style.color = 'black';
    };
  
    render() {
        return (
            <div id="mainGroup">
                <div id="logoBox">
                    <NavLink id="logoLink" exact to="/" >
                        <img alt="indazooLogo" className="aboutIndazooLogo" src={indazooLogo} />
                    </NavLink>
                </div>
                <div id="mainMenu" >
                    <NavLink className="mainMenu" exact to="/" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>HOME</span></NavLink>
                    <NavLink className="mainMenu" exact to="/about" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='VIDEO' ? 'clickedMenu' : ''}>ABOUT</span></NavLink>
                    <div id="artistWebMenu">
                        <Dropdown>
                            <Dropdown.Toggle id="dropdown-custom-components" >
                                ARTIST
                            </Dropdown.Toggle>
                            <Dropdown.Menu>
                                <div className="customDropdowm-item">
                                    <NavLink className="mainMenu" exact to="/artist/kxxzxx" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>kxxzxx</span></NavLink>
                                </div>
                                <div className="customDropdowm-item">
                                    <NavLink className="mainMenu" exact to="/artist/DA" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>D.A</span></NavLink>
                                </div>
                                <div className="customDropdowm-item">
                                    <NavLink className="mainMenu" exact to="/artist/Omondi" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>Omondi</span></NavLink>
                                </div>
                                <div className="customDropdowm-item">
                                    <NavLink className="mainMenu" exact to="/artist/cin" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>c-in</span></NavLink>
                                </div>
                                <div className="customDropdowm-item">
                                    <NavLink className="mainMenu" exact to="/artist/bkkoli" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='ARTIST' ? 'clickedMenu' : ''}>bkkoli</span></NavLink>
                                </div>
                            </Dropdown.Menu>
                        </Dropdown>
                    </div>
                    <NavLink className="mainMenu" exact to="/shop" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='SHOP' ? 'clickedMenu' : ''}>SHOP</span></NavLink>
                    <NavLink className="mainMenu" exact to="/contact" ><span onClick={this.handleMouseClick} onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} className={this.state.clickedMenu==='CONTACT' ? 'clickedMenu' : ''}>CONTACT</span></NavLink>
                </div>
                <div id="mobileToggleIcon" onClick={this._handleMobileMenuClick}>
                    <i className="material-icons mobileMenuIcon">menu</i>
                </div>
                { this.state.mobileMenuClicked ? 
                    <div id="clickedModbileMenu">
                        <div id="closeIcon">
                            <i onClick={this._handleMobileMenuClick} className="fa fa-close"></i>
                        </div>
                        <div className="mobileMenu">
                            <NavLink onClick={this._handleMobileMenuClick} exact to="/" >HOME</NavLink>
                        </div>
                        <div className="mobileMenu">
                            <NavLink onClick={this._handleMobileMenuClick} exact to="/about" >ABOUT</NavLink>
                        </div>
                        <div className="mobileMenu">
                            { this.state.artistMenuClicked ?
                                <i onClick={this._handleArtistMenuClick} className="material-icons verticalMiddle">
                                    remove
                                </i>
                            : 
                            <i onClick={this._handleArtistMenuClick} className="material-icons verticalMiddle">
                                add
                            </i> }
                            <span className="verticalMiddle">
                                ARTIST
                            </span>
                            { this.state.artistMenuClicked ?
                                <div id="subMenuOfArtist">
                                    <div>
                                        <NavLink className="artistName" onClick={this._handleMobileMenuClick} exact to="/artist/kxxzxx">kxxzxx</NavLink>
                                    </div>
                                    <div>
                                        <NavLink className="artistName" onClick={this._handleMobileMenuClick} exact to="/artist/DA">D.A</NavLink>
                                    </div>
                                    <div>
                                        <NavLink className="artistName" onClick={this._handleMobileMenuClick} exact to="/artist/Omondi">Omondi</NavLink>
                                    </div>
                                    <div>
                                        <NavLink className="artistName" onClick={this._handleMobileMenuClick} exact to="/artist/cin" >c-in</NavLink>
                                    </div>
                                    <div>
                                        <NavLink className="artistName" onClick={this._handleMobileMenuClick} exact to="/artist/bkkoli">bkkoli</NavLink>
                                    </div>
                                </div>
                            : ""}
                        </div>
                        <div className="mobileMenu">
                            <NavLink onClick={this._handleMobileMenuClick} exact to="/shop" >SHOP</NavLink>
                        </div>
                        <div className="mobileMenu">
                            <NavLink onClick={this._handleMobileMenuClick} exact to="/contact" >CONTACT</NavLink>
                        </div>
                    </div>
                    : ""}
            </div>
        )
    }
  }

  export default MainMenu;